
'use client';

import { useState, useEffect, useActionState } from 'react';
import { useFormStatus } from 'react-dom';
import { createEventAction, getSessionUser } from '@/lib/actions';
import type { User } from '@/lib/types';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from '@/components/ui/card';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Textarea } from '@/components/ui/textarea';
import { Alert, AlertDescription, AlertTitle } from "@/components/ui/alert";
import { AlertCircle, Loader2, PlusCircle, Home, AlertTriangle } from 'lucide-react';
import { useRouter } from 'next/navigation';
import { useLoading } from '@/contexts/loading-context';
import LoadingLink from '@/components/loading-link';

function SubmitButton() {
  const { pending } = useFormStatus();
  const { startLoading, stopLoading } = useLoading(); 

  useEffect(() => {
    if (pending) {
      startLoading();
    }
    // stopLoading will be handled by the formAction's return or navigation
  }, [pending, startLoading]);

  return (
    <Button type="submit" className="w-full" disabled={pending}>
      {pending ? (
        <>
          <Loader2 className="mr-2 h-4 w-4 animate-spin" /> 正在创建活动...
        </>
      ) : (
        <>
          <PlusCircle className="mr-2 h-4 w-4" /> 创建活动
        </>
      )}
    </Button>
  );
}

export default function CreateEventPage() {
  const router = useRouter();
  const { startLoading, stopLoading } = useLoading();
  const initialState = { message: null, errors: {} };
  
  const [state, dispatch] = useActionState(
    async (prevState: any, formData: FormData) => {
      const result = await createEventAction(prevState, formData);
      if (result && (result.errors || (result.message && !result.success ))) {
        stopLoading(); 
      }
      // If successful, stopLoading will be handled by navigation in LoadingProgressBar
      return result;
    },
    initialState
  );

  const [currentUser, setCurrentUser] = useState<User | null>(null);
  const [isCheckingAuth, setIsCheckingAuth] = useState(true);

  useEffect(() => {
    async function checkUserAuth() {
      setIsCheckingAuth(true);
      startLoading(); 
      try {
        const user = await getSessionUser();
        if (!user) {
          router.push('/login?redirect=/admin/create-event');
          return; 
        } else {
          setCurrentUser(user);
        }
      } finally {
        if (typeof window !== 'undefined' && window.location.pathname === '/admin/create-event') { 
             stopLoading();
        }
        setIsCheckingAuth(false);
      }
    }
    checkUserAuth();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [router]); 
  
  const handleBackToDashboard = () => {
    startLoading();
    router.push('/dashboard');
  };


  if (isCheckingAuth) {
    return (
      <div className="flex justify-center items-center min-h-[calc(100vh-15rem)]">
        <Loader2 className="h-12 w-12 animate-spin text-primary" />
        <p className="ml-4 text-muted-foreground">正在验证权限...</p>
      </div>
    );
  }

  if (!currentUser) { 
    // This case should be handled by the redirect in checkUserAuth,
    // but as a fallback to prevent rendering the form without a user.
    return null; 
  }

  if (currentUser.op !== 1) {
    return (
      <div className="max-w-2xl mx-auto py-8 text-center">
        <Card className="shadow-xl">
          <CardHeader>
            <CardTitle className="text-2xl font-bold">无权访问</CardTitle>
          </CardHeader>
          <CardContent className="space-y-4">
            <Alert variant="destructive">
              <AlertTriangle className="h-4 w-4" />
              <AlertTitle>权限不足</AlertTitle>
              <AlertDescription>
                您没有创建活动的权限。请联系管理员。
              </AlertDescription>
            </Alert>
            <Button onClick={handleBackToDashboard} className="w-full">
              <Home className="mr-2 h-4 w-4" /> 返回仪表盘
            </Button>
          </CardContent>
        </Card>
      </div>
    );
  }

  return (
    <div className="max-w-2xl mx-auto py-8">
      <Card className="shadow-xl">
        <CardHeader>
          <CardTitle className="text-3xl font-bold">创建新活动</CardTitle>
          <CardDescription>填写您的新《我的世界》活动的详细信息。</CardDescription>
        </CardHeader>
        <form action={dispatch}>
          <CardContent className="space-y-6">
            <div className="space-y-2">
              <Label htmlFor="name">活动名称</Label>
              <Input id="name" name="name" placeholder="例如：史诗级建筑大赛" required />
              {state.errors?.name && <p className="text-sm text-destructive">{state.errors.name[0]}</p>}
            </div>

            <div className="space-y-2">
              <Label htmlFor="date">日期和时间</Label>
              <Input id="date" name="date" type="datetime-local" required />
              {state.errors?.date && <p className="text-sm text-destructive">{state.errors.date[0]}</p>}
            </div>

            <div className="space-y-2">
              <Label htmlFor="description">描述 (支持 Markdown)</Label>
              <Textarea
                id="description"
                name="description"
                placeholder="输入活动详情、规则、奖品..."
                rows={8}
                required
              />
              {state.errors?.description && <p className="text-sm text-destructive">{state.errors.description[0]}</p>}
            </div>

            <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
              <div className="space-y-2">
                <Label htmlFor="participantLimit">参与人数上限 (可选)</Label>
                <Input id="participantLimit" name="participantLimit" type="number" placeholder="例如：50 (无限制则留空)" min="1" />
                {state.errors?.participantLimit && <p className="text-sm text-destructive">{state.errors.participantLimit[0]}</p>}
              </div>
              <div className="space-y-2">
                <Label htmlFor="cdKey">活动代码</Label>
                <Input id="cdKey" name="cdKey" placeholder="创建一个唯一的活动代码" required />
                {state.errors?.cdKey && <p className="text-sm text-destructive">{state.errors.cdKey[0]}</p>}
              </div>
            </div>

            <div className="space-y-2">
              <Label htmlFor="imageUrl">图片链接 (可选)</Label>
              <Input id="imageUrl" name="imageUrl" type="url" placeholder="https://example.com/image.png" />
              {state.errors?.imageUrl && <p className="text-sm text-destructive">{state.errors.imageUrl[0]}</p>}
            </div>

            <div className="space-y-2">
              <Label htmlFor="userPassword">您的账户密码 (用于验证)</Label>
              <Input id="userPassword" name="userPassword" type="password" placeholder="输入您的账户密码" required />
              {state.errors?.userPassword && <p className="text-sm text-destructive">{state.errors.userPassword[0]}</p>}
            </div>

            {state.message && !state.errors && ( 
              <p className="text-sm text-green-600 flex items-center"><AlertCircle className="w-4 h-4 mr-1"/> {state.message}</p>
            )}
             {state.message && state.errors && Object.keys(state.errors).length > 0 && !state.errors.userPassword && (
              <p className="text-sm text-destructive flex items-center"><AlertCircle className="w-4 h-4 mr-1"/> {state.message}</p>
            )}
            {/* Specific error for userPassword is handled by the field's error display above */}
          </CardContent>
          <CardFooter>
            <SubmitButton />
          </CardFooter>
        </form>
      </Card>
    </div>
  );
}
